article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button],
input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  width: 100%;
  *zoom: 1; }
  body:before, body:after {
    content: "";
    display: table; }
  body:after {
    clear: both; }

html {
  font-size: 62.5%; }

body {
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  color: #ECF0F1;
  background: url(../images/tile.png); }
  body a {
    color: #ECF0F1;
    text-decoration: none; }
  body p, body label {
    font-weight: 300; }
  body h3 {
    color: #6C28B1;
    font-size: 2.5rem;
    text-align: center; }

.opacity {
  opacity: .2;
  transition: all .2s; }

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  position: relative; }

header {
  transition: all .5s;
  height: 100vh;
  text-align: center; }
  header .tile {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    background: linear-gradient(135deg, #5507A6, #9453D7);
    opacity: 0.9;
    width: 700px;
    height: 90vh;
    margin: 2% auto;
    justify-content: center; }
  header h1 {
    font-size: 6rem;
    padding-top: 10%;
    font-weight: bold; }
  header h2 {
    font-size: 10rem;
    text-transform: uppercase;
    margin-top: 0; }

button {
  background: none;
  color: white;
  border: 3px white solid;
  padding: 10px; }
  button:hover {
    background: #ECF0F1;
    color: #6C28B1; }

.purple, .mobilePurple {
  background: linear-gradient(135deg, #5507A6, #9453D7);
  opacity: 0.9; }

.questions {
  transition: all .5s;
  height: 100vh;
  display: none;
  padding-top: 100px; }
  .questions .formContainer {
    width: 700px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    position: relative;
    z-index: 1; }
    .questions .formContainer .purple {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 2; }
    .questions .formContainer fieldset {
      position: relative;
      z-index: 3;
      border: 0; }
    .questions .formContainer input[type=radio] {
      opacity: 0; }
    .questions .formContainer input[type=radio]:hover + label {
      color: #FFFFFF;
      text-shadow: 0 0 1px #FFFFFF; }
    .questions .formContainer input[type=radio]:checked + label {
      color: #FFD700; }
    .questions .formContainer input[type=submit] {
      margin: 20px;
      background: none;
      color: #ECF0F1;
      margin: 0 auto;
      margin-top: 40px;
      width: 200px;
      height: 50px;
      margin-bottom: 5%;
      position: relative;
      z-index: 3;
      border: 3px white solid; }
    .questions .formContainer input[type=submit]:hover {
      background: #ECF0F1;
      color: #6C28B1;
      text-shadow: 0 0 1px #6C28B1; }
    .questions .formContainer .activity h2 {
      margin-bottom: 0; }
    .questions .formContainer .activity label {
      display: block; }

.answers {
  transition: all .5s;
  display: none;
  padding: 20px; }
  .answers .main-carousel {
    margin: 0; }
  .answers .carousel-cell {
    position: relative;
    width: 700px;
    margin: 0 auto 50px;
    /* arrow color */
    /* hide disabled button */ }
    .answers .carousel-cell .flickity-prev-next-button {
      background: transparent; }
    .answers .carousel-cell .flickity-prev-next-button:hover {
      background: transparent; }
    .answers .carousel-cell .flickity-prev-next-button .arrow {
      fill: white; }
    .answers .carousel-cell .flickity-prev-next-button.no-svg {
      color: white; }
    .answers .carousel-cell .flickity-prev-next-button:disabled {
      display: none; }
    .answers .carousel-cell .imageContainer {
      width: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      padding-bottom: 50%;
      position: relative; }
    .answers .carousel-cell .overlay, .answers .carousel-cell .purple {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0; }
    .answers .carousel-cell .mobilePurple {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0; }
    .answers .carousel-cell .overlay {
      margin-top: 10%;
      padding: 40px; }
      .answers .carousel-cell .overlay h2 {
        text-transform: uppercase;
        letter-spacing: 4px;
        position: relative;
        bottom: 15%;
        margin-bottom: 10px; }
      .answers .carousel-cell .overlay p {
        position: relative;
        bottom: 10%; }
      .answers .carousel-cell .overlay .credit {
        font-size: 1.2rem; }
      .answers .carousel-cell .overlay .colContainer {
        font-weight: 300;
        position: absolute;
        bottom: 0;
        left: 40px;
        right: 40px;
        font-size: 1.2rem;
        border-top: 1px solid #ECF0F1;
        padding: 20px 0; }
        .answers .carousel-cell .overlay .colContainer .col {
          float: left;
          width: 50%; }
          .answers .carousel-cell .overlay .colContainer .col i {
            padding-right: 10px; }
        .answers .carousel-cell .overlay .colContainer .col2 {
          text-align: right; }

footer {
  text-align: center; }
  footer button {
    background: #6C28B1;
    color: white;
    border: 3px #6C28B1 solid;
    padding: 10px;
    transition: all .2s; }
    footer button:hover {
      background: #ECF0F1; }
      footer button:hover a {
        color: #6C28B1; }
  footer p {
    color: grey;
    position: relative;
    bottom: 10px;
    padding: 10px; }
    footer p a {
      color: #666666; }
    footer p a:hover {
      color: grey;
      text-shadow: 0 0 1px grey; }

@media (max-width: 1080px) {
  body {
    background: url(../images/tile.png); }
  .purple {
    opacity: 0.9; }
  header {
    height: auto; }
    header .tile {
      background: linear-gradient(135deg, #5507A6, #9453D7);
      opacity: 0.9;
      width: 700px;
      height: 90vh;
      margin: 2% auto;
      justify-content: center; }
    header h1 {
      font-size: 3rem;
      font-weight: 300; }
  .questions {
    padding-top: 50px; } }

@media (max-width: 700px) {
  header .tile {
    width: 100%;
    height: 100vh;
    margin: 0; }
  .questions {
    padding: 0; }
    .questions .formContainer {
      width: 100%;
      background: none; }
      .questions .formContainer fieldset {
        display: flex;
        flex-direction: column; }
        .questions .formContainer fieldset h2 {
          margin-bottom: 0; }
      .questions .formContainer input[type=radio] {
        height: 30px; }
  .answers {
    padding: 0; }
    .answers .main-carousel {
      width: 100%; }
      .answers .main-carousel .carousel-cell {
        width: 100%; }
        .answers .main-carousel .carousel-cell .overlay {
          margin-top: 0; }
          .answers .main-carousel .carousel-cell .overlay p {
            font-size: 1.4rem; } }

@media (max-width: 620px) {
  .answers .main-carousel .carousel-cell .imgContainer {
    background-position: top;
    padding-bottom: 75%; }
  .answers .main-carousel .carousel-cell .purple {
    display: none; }
  .answers .main-carousel .carousel-cell .mobilePurple {
    display: block; }
  .answers .main-carousel .carousel-cell .overlay {
    position: static;
    color: #6C28B1;
    padding: 10px; }
  .answers .main-carousel .carousel-cell p {
    line-height: 2.5rem;
    text-align: center; }
  .answers .main-carousel .carousel-cell .colContainer {
    border: 0;
    position: relative;
    left: 0;
    right: 0;
    bottom: auto; }
    .answers .main-carousel .carousel-cell .colContainer .col {
      float: none;
      width: 100%;
      text-align: center;
      padding: 5px; }
    .answers .main-carousel .carousel-cell .colContainer a {
      color: #531f87; } }

@media (max-width: 570px) {
  header h2 {
    font-size: 9rem; } }

@media (max-width: 490px) {
  header h2 {
    font-size: 8rem; } }

@media (max-width: 430px) {
  header h2 {
    font-size: 6rem; } }
